<template>
  <div id="app">
    <yd-layout>
        <router-view/>
        
    </yd-layout>
    <vfooter></vfooter>
  </div>
</template>

<script type="text/ecmascript-6">

import {GridsGroup, GridsItem} from 'vue-ydui'
import {APIgetweixinSignature} from './api/api'
import Vfooter from "@/components/vfooter"
import Vheader from "@/components/vheader"
import Vhome from "@/page/home/home"
import wx from 'weixin-js-sdk'  // 引入JS SDK
export default {

  name: 'App',
  components: {
    Vfooter,
    GridsGroup,
    GridsItem,
    Vhome
  },
  methods:{
    getConfig(){
      // 首先获取当前页面的URl 去掉#部分，然后用这个URL向微信请求SDK授权
      let url = location.href.split('#')[0]
      APIgetweixinSignature(
         {
           'url':url
         }          
      ).then(
        r => {
          let res = r.data
          console.log(res)
          console.log('上面是初始化参数')

          this.wxInit(res) 
        }
      ).catch( err => { console.log(err)})
    },
    wxInit(res){ // 微信初始化
    
        let url = location.href.split('#')[0] //获取锚点之前的链接 
        let links = 'https://www.agentily.com'
        let title = 'Agentily'
        let desc = '了解更多房源，请关注“Agentily”公众号'
        let imgUrl = 'https://img14.360buyimg.com/cms/jfs/t14662/221/2020842850/253302/48217853/5a6847d4Ncf2f4037.jpg'

      wx.config({ // 写入配置文件,参数都是后端返回的
          debug: true,
          appId: res.appId,
          timestamp: res.timestamp,
          nonceStr: res.nonceStr,
          signature: res.signature,
          jsApiList: ['onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'onMenuShareWeibo', 'onMenuShareQZone']
        })
      wx.ready( ()=>{ // 核心代码区 验证完成后的函数
            wx.onMenuShareTimeline({
                title: 'share on your timeline', // 分享标题
                desc: 'desc', // 分享描述
                link: 'links', // 分享链接
                imgUrl: 'https://img.huxiucdn.com/auth/data/avatar/001/70/36/69_1499653533.jpg?imageView2/1/w/486/h/486/|imageMogr2/strip/interlace/1/quality/85/format/jpg', // 分享图标
                success: function() {
                  alert("分享到朋友圈成功了")
                  // Toast({
                  //   message: "成功分享到朋友圈"
                  // });
                },
                cancel: function() {
                  alert("分享失败,您取消了分享!")
                  // Toast({
                  //     message: "分享失败,您取消了分享!"
                  // });
                }
              })
      })

       wx.error(function(err) { // 发生错误的回调
          alert(JSON.stringify(err))
        })

    }
  },
  mounted(){
     this.$nextTick( () => {
       this.getConfig() // 异步获取配置参数
     })
  }

}
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "./common/stylus/reset"
#app
  font-family: -apple-system,BlinkMacSystemFont,Helvetica Neue,PingFang SC,Microsoft YaHei,Source Han Sans SC,Noto Sans CJK SC,WenQuanYi Micro Hei,sans-serif
  -webkit-font-smoothing: antialiased
  -moz-osx-font-smoothing: grayscale
  text-align: center
  color: #2c3e50
  font-size :16px
  line-height :20px
  height :100%
body{
  padding-bottom:50px;
  z-index :-10;
  background :#f5f7fa!important
}
.yd-flexview{z-index:10;max-width:1000px}
.bigtitle{font-size:25px;margin: 20px auto;}
.smalltitle{font-size:20px; margin: 15px auto; text-align: left; margin-left: 20px;}
.yd-navbar {height: 50px!important}
.yd-navbar-center-title{font-size: 20px!important;}
.yd-navbar-center-box{height:50px!important;}

.actionbtn{
  border-radius 50%;
  -webkit-box-shadow: 0 4px 9px 0 rgba(213,218,224,.3);
  box-shadow: 0 4px 9px 0 rgba(213,218,224,.3);
  -webkit-transition: opacity .25s ease,-webkit-transform .25s ease,-webkit-filter .25s ease;
  transition: opacity .25s ease,-webkit-transform .25s ease,-webkit-filter .25s ease;
  transition: transform .25s ease,filter .25s ease,opacity .25s ease;
  transition: transform .25s ease,filter .25s ease,opacity .25s ease,-webkit-transform .25s ease,-webkit-filter .25s ease;
  min-width :50px
  min-height :50px;
  line-height :50px
}

</style>



